import { Router } from "react-router-dom";
import React, { useState } from 'react';
import './Card.css'
import 'bootstrap/dist/css/bootstrap.min.css';
import content from './content';

export default function InnerCard (props){

    

 function muted(){
     if (props.val===0)
      return 'text-muted'
    else if(props.val==1)
     return ''
     else return ''
}
    

    return(
        <div className="col-lg-4">
          <div className="card mb-5 mb-lg-0">
            <div className="card-body">
                {console.log(content[0].type[props.val])}
                
                <h5 className="card-title text-muted text-uppercase text-center">{content[0].type[props.val]}</h5>
              <h6 className="card-price text-center">${content[0].cost[props.val]}<span className="period">/month</span></h6>
              <hr/>
              <ul className="fa-ul">
                <li><span className="fa-li"></span>{content[0].content[0]}</li>
                <li><span className="fa-li"></span>{content[0].content[1]}</li>
                <li><span className="fa-li"></span>{content[0].content[2]}</li>
                <li><span className="fa-li"></span>{content[0].content[0]}</li>
                <li className={muted()}><span className="fa-li"></span>{content[0].content[3]} </li>
                <li className={muted()}><span className="fa-li"></span>{props.extra} {content[0].content[4]} </li>
                <li className={muted()}><span className="fa-li"></span> {content[0].content[5]} </li>
                <li className={props.appproperty}><span className="fa-li"></span>{content[0].content[6]} </li>
              </ul>
              <div className="d-grid">
                <a href="#" className="btn btn-primary text-uppercase">Button</a>
              </div>
            </div>
          </div>
        </div>
      
      


    )
}

