import { Router } from "react-router-dom";
import React, { useEffect } from 'react';
import './Card.css'
import 'bootstrap/dist/css/bootstrap.min.css';
import content from './content';
import InnerCard from "./Innercard";




function Card (props){

  return(
      <div className='pricing py-5'>
          <div className='container'>
          <div className="row">
          <InnerCard  appproperty ='text-muted' extra=''  val = {0}  />,
          <InnerCard  appproperty ='text-muted' extra=''  val = {1}  />,
          <InnerCard  appproperty =''           extra={<b>Unlimited</b>}   val = {2}  />,
           </div>
          </div>
      </div>
        
  )


}
export default Card;